/* Responsive styles starts here */
@media only screen and (max-width:1367px){
.pricing_col_sec ul {
    padding: 0;
}
}

@media only screen and (max-width:1024px){
    .main-banner .carousel-cell {
        height: auto;
    }
    .pricing_col_sec .h1 {
        font-size: 36px !important;
    }
}

@media only screen and (max-width:992px){

    #school-page .slider-cards .h1 {
        font-size: 200% !important;
        margin-bottom: 10px;
    }
    #school-page .slider-cards .h6 {
        margin-bottom: 10px;
    }

    #school-page .btn-lg{
        font-size: 16px;
    }
    img[class*='flex-'][class*='-grow-1'] {
        object-fit: contain !important;
    }
    .navbar .sign-in-btn {
        margin-top: 10px;
        margin-left: 0 !important;
        width: 102px;
    }
    .main-banner .carousel-cell {
        height: auto;
    }
    .SpacesHeroSectionOpensource h1 {
        font-size: 240% !important;
    }
    .SpacesHeroSectionOpensource .btn-violet {
    margin-left: 0 !important;
    margin-top: 15px;
    }
    #member-gallery h5 {
        font-size: 1rem;
    }
}

@media only screen and (max-width:767px){
    .col-sm-text-center {
        text-align: center;
    }
    .css-llvukp {
        right: -20 !important
    }
    #terms-page h1, #privacy-page h1 {
        font-size: 240% !important;
    }
    #terms-page h3, #terms-page h4, #privacy-page h3, #privacy-page h4 {
        text-align: left;
    }
    .SpacesHeroSectionOpensource{
        text-align: center;
    }
    .roverz_img {
        margin: 0 auto;
        margin-bottom: 15px;
    }
    .SpacesHeroSectionHome .bannerimg{
        display: none;
    }
    .play-appstore-cont {
        justify-content: center;
    }
    .SpacesHeroSectionHome.main-banner h1 {
        text-align: center;
    }
    #document-sec, #task-sec, #messaging-sec, #screenshot-sec, #getapp {
        padding: 0 50px;
        text-align: center;
    }
    #screenshot-sec img {
        margin-bottom: 50px;
    }
    
}



@media only screen and (max-width:480px){
    #school-page .btn-lg{
        font-size: 16px;
        margin-bottom: 10px;
    }

    #school-page .carousel-cell{
        text-align: center;
    }
    #school-page .carousel-cell h6{
        margin-bottom: 5px;
    }
    .section-prefooter .list-social-links i {
        font-size: 14px;
        line-height: 12px;
    }
    .SpacesHeroSectionHome.main-banner h1 {
        font-size: 200% !important;
    }
    .h1 {
        font-size: 240% !important;
    }
    .section-prefooter ul li {
        margin-bottom: 15px;
    }
    #messaging-page ul.list-group {
        text-align: left;
    }
    #document-sec, #task-sec, #messaging-sec, #screenshot-sec, #getapp {
        padding: 0 20;
        text-align: center;
    }
}
/* Responsive styles ends here */


